html{
    /* rem */
    font-size:14px;
    /*  IEwidthpaddingborder */
    box-sizing: border-box;

}

html,body{
    /*  */
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    /*   */
    -webkit-overflow-scrolling: touch;
    overflow-scrolling: touch;
    /*  */
    height: 100%;
}

body{
    /*  */
    background: #fff;
    /*  */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* marginpadding,  */
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dd,
ul,
ol,
th,
td,
button,
figure,
input,
textarea,
form,
pre,
blockquote,
figure{
    margin: 0;
    padding: 0;
}

a{
    /*  */
    cursor: pointer;
    /*  */
    text-decoration:none;
    /* antd   */
    transition: color 0.3s ease;
}

ol,
ul{
    /* ugly */
    list-style:none
}

/* outline */
a,
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
button,
textarea {
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    color: inherit;
    outline: none;
}

button,
input[type='submit'],
input[type='button'] {
    /*  */
    cursor: pointer;
}
/*  apperance */
input[type='number'] {
    -moz-appearance: textfield;
}
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    margin: 0;
    -webkit-appearance: none;
}
/**
 * Firefox
 */
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}
/**
 * html5hiddenIE10
 */

[hidden] {
    display: none;
}
template {
    /* template template */
    display: none;
}
